.overall-container {
    padding-bottom: .1em;
    padding-top: .1em;
    width: 30em;
    border: 1px solid #2e2e2e21;
    display: grid;
    grid-template-rows: 30% 65%;
}

.retweet-width {
    width: 30em;
}

.standard-width {
    width: 38.8em;
}

.tweet-container {
    min-height: 10em;
    display: grid;
    grid-template-rows: 70% 15%;
}

.tweet-content-wrapper {
    padding-left: 2em;
}

.tweet-header {
    font-weight: 650;
}

.tweet-text {
    font-size: 1em;
}

.text-green {
    color: #56cd5c;
}

.buttons { 
    margin-left: 6em;
    padding: 1em;
    display: flex;
    padding-right: 0em;
    padding-top: 0em;
    margin-bottom: 1em;
    position: relative;
    justify-content: bottom;
    font-size: 1em;
    width: 50%;
}

.custom-btn {
    font-size: 1em;
    padding: 0.5em;
    margin: 0;
}

.btn-icon {
    padding-left: 1em;
    padding-right: 2em;
}

.rt-icon:hover {
    color: #56cd5c;
}

.like-icon:hover {
    color: #c40039;
}

.red-text {
    color: #c40039;
}


.retweet-btn {
    position: absolute;
    right: 0;
    padding-right: 2em;
}


.retweet {
    width: 30em;
}

.retweet-container {
    font-size: 1em;
}

.retweet-header {
    margin-bottom: 0;
    padding-left: 3em;
    font-size: .95em;
}

.profile-picture {
    margin-left: 1em;
    padding-top: 1em;
}

.pointer {
    cursor: pointer;
}
